.lower_bg{
  position: absolute;
  width: 100%;
  height: 100px;
}

.lower_bg::before{
  content: " ";
  position: absolute;
  width: 100%;
  bottom: -200px;
  z-index: 50;
  height: 200px;
  top: 50px;
  background-color: black;
}

.spinner{
  width: 40px;
  height: 40px;
  border: 2px solid rgb(197, 191, 191);
  border-radius: 50%;
  border-top: 2px solid rgb(44, 93, 197);
}

